import isPlainObject from 'lodash/isPlainObject';
import { anyChanged } from './utils'; // 对比2组数据源，返回变化的部分

export function compareDataSource(pre, next) {
  if (!pre || !next) {
    return next;
  }

  var result = {};
  var nextKeys = Object.keys(next);
  nextKeys.forEach(function (key) {
    if (!pre[key]) {
      result[key] = next[key];
    } else if (anyChanged(pre[key], next[key])) {
      result[key] = next[key];
    }
  });
  return result;
}
/**
 * 是否是一个fetch的接口
 * @param opt
 */

export function isFetchOptions(opt) {
  if (typeof opt === 'string') {
    return true;
  }

  if (isPlainObject(opt) && opt.url) {
    return true;
  }

  return false;
}
/**
 *
 * 模板检测
 * @param opt { tpl: '<%=abc%>'}
 */

export function isTplOptions(opt) {
  if (isPlainObject(opt) && opt.tpl && typeof opt.tpl === 'string') {
    return true;
  }

  return false;
}